<template>
  <div class="attackTracking">
    <TOP title="攻击来源回溯" />
    <!-- 时间段和应用 -->
    <div class="right box_num">
      <el-date-picker
        :clearable="false"
        v-model="dataPickerVal"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions"
        @change="onDataChange"
        popper-class="myDatepicker"
      >
      </el-date-picker>
    </div>
    <div class="left box_num">
      <div class="title">IP: {{ ip }}</div>
    </div>
    <!-- 攻击源概述 -->
    <div class="box_attack_info">
      <div class="col title">
        <span>攻击源概述</span>
      </div>
      <div class="col details">
        <p class="count">
          <span>攻击次数：</span>
          <count-to
            class="number count_to"
            :innerSpan="true"
            :start-val="0"
            :end-val="attackCount"
            separator=""
          />
        </p>

        <p><span>IP来源：</span> {{ ip_address }}</p>
        <p>
          <span>地理坐标：</span>
          {{ ip_geo }}
          <i
            v-if="ip_geo !== 'NaN/NaN'"
            @click="dialogGeo = !dialogGeo"
            style="color: #4d8cff;font-size:18px; cursor:pointer; "
            class="el-icon-circle-plus"
          ></i>
        </p>
        <p><span>影响范围：</span> {{ ip_city }}个城市 {{ ip_app }}个应用</p>
      </div>
      <div class="col whois">
        <a
          :href="
            `http://ipwhois.cnnic.net.cn/bns/query/Query/ipwhoisQuery.do?txtquery=${ip}&queryOption=ipv4`
          "
          target="_blank"
          >更多信息请进行IP Whois查询 ></a
        >
      </div>
    </div>
    <div class="box_pie">
      <boxBg />
      <span class="title">攻击方式占比</span>
      <v-chart class="chart" ref="pie" :options="chartPieOpts" />
    </div>

    <div class="box_wordCloud city">
      <boxBg />
      <span class="title">城市威胁标签</span>
      <v-chart
        class="chart"
        ref="wordCloudCity"
        :options="chartWordCloudCityOpts"
      />
    </div>

    <div class="box_wordCloud website">
      <boxBg />
      <span class="title">站点威胁标签</span>
      <v-chart
        class="chart"
        ref="wordCloudWebsite"
        :options="chartWordCloudWebsiteOpts"
      />
    </div>
    <div class="box_timeline">
      <span class="title">
        攻击路径
      </span>
      <div class="floatRight">
        <i class="dot lg"></i>高危应用 <i class="dot md"></i>中危应用
        <i class="dot sm"></i>低危应用
      </div>
      <div class="timeline">
        <div class="start">现在</div>
        <div class="list" ref="listScroll">
          <div
            class="item"
            v-for="(item, index) in attackList"
            :key="index"
            @click="showDetails(index)"
          >
            <div class="time" :class="item.level">{{ item.time }}</div>
            <div class="name">{{ item.app }}</div>
            <div class="tag">
              <span :class="item.level">{{ item.type }}</span>
            </div>
          </div>
        </div>
        <div class="end" v-if="isOnBottom">
          <p>
            <span>
              <i :class="pageLoading ? 'el-icon-loading' : 'el-icon-more'"></i>
            </span>
            <span class="btn" @click="loadMore" v-if="!pageLoading">
              查看更多</span
            >
          </p>
        </div>
      </div>
    </div>
    <el-dialog
      class="myDialog"
      :visible.sync="dialogGeo"
      width="1040px"
      :modal-append-to-body="false"
    >
      <span slot="title">地理坐标</span>
      <v-chart
        style="width:1000px; height:500px"
        ref="map"
        :options="mapOpts"
      />
    </el-dialog>

    <el-dialog
      class="myDialog"
      :visible.sync="dialogShow"
      width="50%"
      :modal-append-to-body="false"
    >
      <span slot="title">详情</span>
      <div class="list" v-if="dialogData">
        <div class="item" style="width:50%">
          应用名称：{{ dialogData.site_name || "无" }}
        </div>
        <div class="item" style="width:50%">
          攻击时间：{{ fmtDate("Y-M-D hh:mm:ss", dialogData.attack_time) }}
        </div>
        <div class="item" style="width:50%">
          攻击类型：{{ dialogData.attack_type || "无" }}
        </div>
        <div class="item" style="width:50%">
          请求方法：{{ dialogData.request_method || "无" }}
        </div>
        <div class="item">请求地址：{{ dialogData.request_uri || "无" }}</div>
        <div class="item">COOKIE：{{ dialogData.cookie || "无" }}</div>
      </div>
      <span slot="footer" class="dialog-footer">
        <span @click="changeDialog(-1)" class="btn" v-if="dialogDataIndex !== 0"
          >上一页</span
        >
        <span
          @click="changeDialog(1)"
          class="btn"
          v-if="dialogDataIndex < attackList.length - 1"
        >
          下一页
        </span>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import empty from "@/model/chart_null.js";

import opts_map, { dot } from "@/model/chart_world.js";
import position from "@/model/position.json";

import wordCloud from "@/model/wordCloud.js";
import opts_pie, { fmtSeries } from "@/model/chart_pie.1.js";
import { fmtNumber, fmtDate } from "@/utils/utils";
import { api, asyncFn, apiPost } from "@/api/api";

import TOP from "@/components/box_top";
import boxBg from "@/components/box";
export default {
  name: "attackTracking",
  components: {
    TOP,
    boxBg
  },
  data() {
    return {
      dialogGeo: false,
      mapOpts: opts_map,
      dialogShow: false,
      dialogDataIndex: -1,
      dialogData: null,
      dataPickerVal: [
        new Date(+new Date() - 3600 * 1000 * 24 * 30),
        new Date()
      ], // 选择的时间段
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      pageLoading: false, // 时间轴加载中
      pageIndex: 1, // 时间轴页码
      pageSize: 30, // 时间轴每页数量
      attackList: [], // 时间轴数据
      isOnBottom: false, // 时间轴是否到底部了
      isMoreAttack: true, // 是都存在下一页时间轴数据
      attackCount: 0,
      ip_address: "未知",
      ip_geo: "NaN/NaN",
      ip_city: 0,
      ip_app: 0,
      // startTime: fmtDate("Y-M-D", false, -30) + " 00:00:00",
      // endTime: fmtDate("Y-M-D") + " 23:59:59",
      timerPie: null,
      chartPieOpts: empty,
      chartWordCloudCityOpts: empty,
      chartWordCloudWebsiteOpts: empty,
      id: this.$route.query.id,
      ip: this.$route.query.ip,
      highAttack: 1234,
      chart: null
    };
  },
  computed: {
    startTime() {
      return fmtDate("Y-M-D", this.dataPickerVal[0]) + " 00:00:00";
    },
    endTime() {
      return fmtDate("Y-M-D", this.dataPickerVal[1]) + " 23:59:59";
    },
    highAttackArray() {
      return fmtNumber(this.highAttack, 99999999).split("");
    }
  },
  created() {
    if (this.$route.query.ip) {
      this.refreshAPI();
      this.loadPage();
    } else {
      this.$router.replace({ name: "securityMonitor" });
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.listScroll.addEventListener("scroll", this.onListScroll);
    });
  },
  methods: {
    fmtDate,
    // 日期变化 全图刷新
    onDataChange() {
      this.refreshAPI();
    },
    changeDialog(i) {
      this.dialogDataIndex += i;
      this.dialogData = this.attackList[this.dialogDataIndex].origin;
    },
    // 时间轴弹窗
    showDetails(index) {
      this.dialogDataIndex = index;
      this.dialogData = this.attackList[index].origin;
      this.dialogShow = true;
    },
    loadMore() {
      this.pageIndex++;
      this.loadPage();
    },
    onListScroll(e) {
      // 判断最近一次获取数据的数量是否===pageSize：30
      // 时间轴是否滚动到了底部
      if (
        this.isMoreAttack &&
        e.target.scrollHeight - 30 < e.target.scrollTop + e.target.offsetHeight
      ) {
        this.isOnBottom = true;
      } else this.isOnBottom = false;
    },
    async loadPage() {
      this.pageLoading = true;
      try {
        let res = await apiPost(api.getAttackInfo, {
          remoteip: this.ip,
          startTime: this.startTime,
          endTime: this.endTime,
          pageIndex: this.pageIndex,
          pageSize: this.pageSize
        });
        if (res.data && res.data.length > 0) {
          let item = res.data[0];

          this.ip_address = `${item.location.country_cn_name || "未知国家"} 
          ${item.location.region_name || "未知区域"} 
          ${item.location.city || "未知城市"}`;
          // 定位 城市 > 省份 》 国家
          this.ip_geo = "NaN/NaN";
          // 城市 全匹配
          let geo_1 = position[item.location.city];
          // 城市减一个字 再 全匹配
          let geo_2 =
            position[
              item.location.city.slice(0, item.location.city.length - 1)
            ];

          let geo_3 = position[item.location.region_name];
          let geo_4 =
            position[
              item.location.region_name.slice(
                0,
                item.location.region_name.length - 1
              )
            ];

          let geo_5 = position[item.location.country_cn_name];
          let geo_6 =
            position[
              item.location.country_cn_name.slice(
                0,
                item.location.country_cn_name.length - 1
              )
            ];

          if (geo_1) this.ip_geo = geo_1.join("/");
          else if (geo_2) this.ip_geo = geo_2.join("/");
          else if (geo_3) this.ip_geo = geo_3.join("/");
          else if (geo_4) this.ip_geo = geo_4.join("/");
          else if (geo_5) this.ip_geo = geo_5.join("/");
          else if (geo_6) this.ip_geo = geo_6.join("/");

          if (this.ip_geo !== "NaN/NaN") {
            let tmp = JSON.parse(JSON.stringify(opts_map));
            dot.data[0].name = this.ip_address;
            dot.data[0].value = this.ip_geo.split("/");
            tmp.series[0] = dot;
            this.mapOpts = tmp;
          }

          this.updateTimeline(res.data);
        }
      } catch (error) {
        console.log(error);
      }
      this.pageLoading = false;
    },
    refreshAPI() {
      asyncFn(api.getAttackTypeAndLevel, {
        remoteip: this.ip,
        startTime: this.startTime,
        endTime: this.endTime
      }).then(i => {
        this.updatePie(i);
      });

      asyncFn(api.getThreatLabel, {
        remoteip: this.ip,
        startTime: this.startTime,
        endTime: this.endTime
      }).then(data => {
        this.initWordCloudCity(data.cityThreatLabel);
        this.initWordCloudWebsite(data.siteThreatLabel);
      });
    },
    updateTimeline(data) {
      this.isOnBottom = data.length === 0;
      this.isMoreAttack = data.length >= this.pageSize;
      this.attackList = this.attackList.concat(
        data.map(item => {
          return {
            origin: item,
            level: item.attack_level || "low",
            time: fmtDate("Y-M-D hh:mm:ss", item.attack_time),
            app: item.site_name,
            type: item.attack_type
          };
        })
      );
    },
    // 异步更新
    updatePie(data) {
      if (JSON.stringify(data.attackLevel) === "{}") {
        return (this.chartPieOpts = empty);
      }
      let tmp = JSON.parse(JSON.stringify(opts_pie));
      let high = data.attackLevel.high || 0;
      let medium = data.attackLevel.medium || 0;
      let low = data.attackLevel.low || 0;

      tmp.series[0].data[0].value = high;
      tmp.series[0].data[1].value = medium;
      tmp.series[0].data[2].value = low;

      tmp.series[1].data = fmtSeries(data.attackType);
      this.attackCount = high + medium + low;
      this.chartPieOpts = tmp;

      if (this.timerPie === null) this.$nextTick(this.animationPie);
    },
    animationPie() {
      let dataIndex = -1;
      let pie = this.$refs.pie;
      let dataLen = pie.options.series[1].data.length;
      let fn = () => {
        pie.dispatchAction({
          type: "downplay",
          seriesIndex: 1,
          dataIndex
        });
        dataIndex = (dataIndex + 1) % dataLen;
        pie.dispatchAction({
          type: "highlight",
          seriesIndex: 1,
          dataIndex
        });
        // 显示 tooltip
        pie.dispatchAction({
          type: "showTip",
          seriesIndex: 1,
          dataIndex
        });
      };
      fn();
      this.timerPie = setInterval(fn, 5 * 1000);
    },
    fmtData(list) {
      let colors = [
        "#4F93FF",
        "#0DC1FF",
        "#60E9EA",
        "#28C771",
        "#FEC42C",
        "#DD4444",
        "#DD4D79",
        "#8622F7"
      ];
      return list.map((i, index) => {
        return {
          name: i,
          value: Math.round(Math.random() * 255),
          textStyle: {
            normal: {
              color: colors[index % colors.length]
            }
          }
        };
      });
    },
    initWordCloudCity(data) {
      if (JSON.stringify(data) === "{}") {
        return (this.chartWordCloudCityOpts = empty);
      }
      let opts = JSON.parse(JSON.stringify(wordCloud));
      let list = [];
      for (let key in data) {
        list.push({
          name: key,
          value: data[key]
        });
      }
      list.sort((a, b) => b.value - a.value);
      list = list.map(i => i.name);

      this.ip_city = list.length;

      opts.series[0].name = "城市威胁标签";
      opts.series[0].data = this.fmtData(list);
      this.chartWordCloudCityOpts = opts;
    },
    initWordCloudWebsite(data) {
      if (JSON.stringify(data) === "{}") {
        return (this.chartWordCloudWebsiteOpts = empty);
      }
      let opts = JSON.parse(JSON.stringify(wordCloud));
      let list = [];
      for (let key in data) {
        list.push({
          name: key,
          value: data[key]
        });
      }
      list.sort((a, b) => b.value - a.value);
      list = list.map(i => i.name);
      this.ip_app = list.length;

      opts.series[0].name = "站点威胁标签";
      opts.series[0].data = this.fmtData(list);
      this.chartWordCloudWebsiteOpts = opts;
    }
  }
};
</script>

