let color = [
    ' rgba(127, 36, 239, 1)',
    ' rgba(179, 85, 233, 1)',
    ' rgba(211, 77, 121, 1)',
    ' rgba(194, 67, 77,  1)',
    ' rgba(247, 153, 55, 1)',
    ' rgba(251, 193, 46, 1)',
    ' rgba(212, 223, 90, 1)',
    ' rgba(40, 190, 115, 1)',
    ' rgba(120, 195, 197,1)',
    ' rgba(3, 174, 235,  1)',
    ' rgba(81, 139, 233, 1)',
];

export const fmtSeries = obj => {
    let list = [];

    Object.keys(obj).forEach((key, index) => {
        let item = {
            name: key,
            value: obj[key],
        };

        let rgb = color[index % 11];
        let rgba = rgb.replace('1)', '0.2)');
        item.itemStyle = {
            normal: {
                color: rgba,
                borderColor: rgb,
            },
            emphasis: {
                color: rgb,
            },
        };
        item.labelLine = {
            lineStyle: {
                color: rgb,
            },
        };
        item.label = {
            formatter: '{b}',
            color: '#fff',
        };
        list.push(item);
    });
    return list;
};

export default {
    tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)',
    },
    color: color,
    series: [
        {
            name: '攻击方式占比',
            type: 'pie',
            selectedMode: 'single',
            minAngle: 10, //最小的扇区角度（0 ~ 360），用于防止某个值过小导致扇区太小影响交互
            radius: ['0', '35%'],

            label: {
                normal: {
                    position: 'inner',
                },
            },
            labelLine: {
                normal: {
                    show: false,
                },
            },
            data: [
                { value: 0, name: '高危攻击', itemStyle: { color: '#0A2AEF' } },
                { value: 0, name: '中危攻击', itemStyle: { color: '#2A6AFF' } },
                { value: 0, name: '低危攻击', itemStyle: { color: '#4D8CFF' } },
            ],
        },
        {
            name: '攻击方式占比',
            type: 'pie',
            minAngle: 10, //最小的扇区角度（0 ~ 360），用于防止某个值过小导致扇区太小影响交互
            radius: ['45%', '55%'],
            data: [],
        },
    ],
};
